<template>
  <div class="mall">
    <div class="search">
      <img src="../assets/images/mall_search@2x.png" />
      <input type="text" placeholder="搜索平台精选好物" />
    </div>
    <div class="js-image-swiper custom-image-swiper-single" data-images="1">
      <swip :lists="bannerLists" v-if="bannerLists"></swip>
    </div>
    <div class="hot_sell">
      <img src="../assets/images/mall_title@2x.png" />
    </div>
    <div class="category clearfix">
      <a class="left">
        <img :src="hotLists[0].ad_code" />
      </a>
      <div class="right">
        <a>
          <img :src="hotLists[1].ad_code" />
        </a>
        <a>
          <img :src="hotLists[2].ad_code" />
        </a>
      </div>
    </div>
    <div class="hot_goods">
      <ul class="clearfix">
        <li v-for="item in productList" :key="item.goods_id">
          <div class="goods_item">
            <router-link
              :to="{name:'product',query:{
              id:item.goods_id,
            }}"
            >
              <div class="img_box">
                <img :src="item.original_img" />
              </div>
              <div class="detail">
                <h1>{{item.goods_name}}</h1>
                <span class="price">￥{{item.shop_price}}</span>
                <span class="o_price">{{item.market_price}}</span>
                <p class="sale">{{item.sales_sum}}件已售</p>
                <i></i>
              </div>
            </router-link>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import swip from "./Swiper.vue";
import axios from "axios";
import url from "@/axios/api.js";
import qs from "qs";
export default {
  name: "Mall",
  data() {
    return {
      bannerLists: null,
      hotLists: null,
      productList: null
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      axios.post(url.index).then(res => {
        this.bannerLists = res.data.data.carousel;
        this.hotLists = res.data.data.hotGoods;
        this.productList = res.data.data.goods_list.list;
      });
    }
  },
  components: {
    swip
  }
};
</script>

<style>
.clearfi::after {
  content: "";
  display: block;
  clear: both;
}

.mall {
  min-height: 860px;
  padding: 10px;
  overflow: scroll;
}
.search {
  padding: 13px;
  background: rgba(255, 255, 255, 1);
  border-radius: 16px;
  display: flex;
}

.search img {
  width: 17px;
  height: 17px;
}

.search input {
  border: none;
  margin-left: 10px;
}

.search input::placeholder {
  font-size: 12px;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: rgba(133, 133, 133, 1);
  line-height: 12px;
}

.hot_sell {
  width: 147px;
  height: 32px;
  margin: 10px auto;
}

.hot_sell img {
  height: 100%;
  width: 100%;
}
.clearfix::after {
  content: "";
  display: block;
  clear: both;
}
.category img {
  height: 100%;
  width: 100%;
}
.category .left {
  display: block;
  width: 170px;
  height: 210px;
  float: left;
}
.category .right {
  float: right;
}

.category .right a {
  display: block;
  height: 100px;
  width: 170px;
  margin-bottom: 5px;
}
a {
  text-decoration: none;
}
.hot_goods ul {
  margin-bottom: 50px;
}
.hot_goods li {
  list-style: none;
  float: left;
  width: 50%;
  box-sizing: border-box;
  padding-bottom: 6px;
}
.hot_goods li:nth-child(odd) {
  padding-right: 3px;
}
.hot_goods .img_box {
  margin-bottom: 10px;
  width: 170px;
  height: 173px;
}

.hot_goods .img_box img {
  width: 100%;
  height: 100%;
}
.hot_goods .detail {
  position: relative;
  background: #fff;
  padding: 10px 0;
}
.hot_goods .detail h1 {
  font-size: 13px;
  font-family: SourceHanSansCN-Regular;
  font-weight: 400;
  color: rgba(0, 0, 0, 1);
  line-height: 15px;
}

.hot_goods .detail .price {
  font-size: 13px;
  font-family: SourceHanSansCN-Bold;
  font-weight: bold;
  color: rgba(238, 36, 30, 1);
  line-height: 15px;
}

.hot_goods .detail .o_price {
  width: 46px;
  height: 9px;
  font-size: 11px;
  font-family: SourceHanSansCN-Regular;
  font-weight: 400;
  text-decoration: line-through;
  color: rgba(123, 123, 123, 1);
  line-height: 15px;
}

.hot_goods .detail .sale {
  width: 74px;
  height: 11px;
  font-size: 11px;
  font-family: SourceHanSansCN-Regular;
  font-weight: 400;
  color: rgba(123, 123, 123, 1);
  line-height: 15px;
}

.hot_goods .detail i {
  background: url(../assets/images/mall_cart@2x.png) no-repeat;
  background-size: 100% 100%;
  display: block;
  width: 25px;
  height: 25px;
  position: absolute;
  right: 10px;
  bottom: 0;
}

.swp-page {
  height: 155px;
}
</style>



